.root {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 100px;
}
.document {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}
.container {
  width: 100%;
  max-width: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.prev {
  width: 300px;
  cursor: pointer;
  transition: width 0.3s ease-in-out;
}
.header {
  font-size: 30px;
  font-weight: 600;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.button {
  width: 150px;
  height: 50px;
  border: 2px solid black;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #ffffff;
  cursor: pointer;
  font-size: 16px;
  font-weight: 600;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  margin-bottom: 100px;
  text-decoration: none;
}

@media (min-width: 950px) {
  .root {
    flex-direction: row;
    margin-top: 150px;
  }
  .container {
    max-width: none;
    height: 500px;
    width: 500px;
  }
  .prev:hover {
    width: 350px;
    transition: width 0.3s ease-in-out;
  }
  .document {
    margin: 0;
  }
  .button:hover {
    background-color: rgba(244, 101, 36, 1);
    color: white;
    border: none;
  }
}
